        window.open('https://www.wordsquare.org/square-crossroads/assessments');
    },
    story_page:function(){
        this.$router.push('/story');
    },
    share_fun: async function(){
        try {
            await navigator.share({
                title: "King     ?",
                url: "",
            });
            console.log(" ");
        } catch (e) {
            var ans=confirm('url ?');
            if(ans){
                var url = '';
                var textarea = document.createElement("textarea");
                document.body.appendChild(textarea);
                url = location.origin+location.pathname;
                textarea.value = url;
                textarea.select();
                document.execCommand("copy");
                document.body.removeChild(textarea);
            }
        }
    }
  },
  mounted(){
    var idx = this.$route.query.idx;
    this.result_obj=this.result_arr[idx];
    setTimeout(() => {
        this.loading=false;
    }, 5000);
      
    // section17   
    setTimeout(() => {
        $('.section17>.back>img').addClass('on');
        $('.section17>.txt_box').addClass('on');
    }, 500);
    var default_on=$('.audio_area>.wrap>.btn_area>.right_area>.switch').find('.music_on');
    var default_off=$('.audio_area>.wrap>.btn_area>.right_area>.switch').find('.music_off');
    
    var video = document.getElementById('audio_id'); 
    var is_play=video.paused;
    if(is_play){
        default_on.hide();
        default_off.hide();
        default_on.show();
    }else{
        default_on.hide();
        default_off.hide();
        default_off.show();
    }
    $('.audio_area>.wrap>.btn_area>.right_area>.switch').click(function(){
        var that = $(this);
        var on = that.find('.music_on');
        var off = that.find('.music_off');
        var on_status = on.css('display');
        var off_status = off.css('display');
        if(on_status=='none' && off_status=='block'){
            on.hide();
            off.hide();
            on.show();
            document.getElementById('audio_id').pause();
        }else if(on_status=='block' && off_status=='none'){
            on.hide();
            off.hide();
            off.show();
            document.getElementById('audio_id').play();
        }
    })

  }
}
